  display: list-item;
}

/* Misc
   ========================================================================== */
/**
 * Add the correct display in IE 10+.
 */
template {
  display: none;
}

/**
 * Add the correct display in IE 10.
 */
[hidden] {
  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

ul {
  list-style: none;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.alinear-centro {
  display: flex;
  justify-content: center;
}

.sombra {
  border: 1px solid #c8c8c8;
  box-shadow: 5px 5px 13px #959595;
  margin-bottom: 2rem;
}

.boton-amarillo-block {
  display: block;
  background-color: #E08709;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 3rem;
  margin-top: 2rem;
  border: none;
  cursor: pointer;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
}

.boton-amarillo {
  display: inline-block;
  background-color: #E08709;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 3rem;
  margin-top: 2rem;
  border: none;
  cursor: pointer;
}
.boton-amarillo:hover {
  background-color: #c77808;
}

.boton-verde {
  display: inline-block;
  background-color: #71B100;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 3rem;
  margin-top: 2rem;
  border: none;
  cursor: pointer;
}
.boton-verde:hover {
  background-color: #619800;
}

.boton-rojo {
  display: inline-block;
  background-color: red;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 3rem;
  margin-top: 2rem;
  border: none;
  cursor: pointer;
}
.boton-rojo:hover {
  background-color: #e60000;
}

.ocultar-boton {
  display: none;
}

/** UI **/
/* FORMULARIO */
.contenedor-formulario {
  margin: 2rem;
}
.contenedor-formulario p, .contenedor-formulario li {
  font-size: 1.6rem;
}
.contenedor-formulario .contenedor-lista {
  margin: 2rem 0;
}
.contenedor-formulario .contenedor-lista li {
  padding-bottom: 0.5rem;
  display: flex;
}
.contenedor-formulario .contenedor-lista li::before {
  content: url(../../../assets/images/icon-list.svg);
  padding-right: 2rem;
}
.contenedor-formulario .formulario {
  display: flex;
  flex-direction: column;
}
.contenedor-formulario .formulario .contenedor-label {
  display: flex;
  justify-content: space-between;
}
.contenedor-formulario .formulario label, .contenedor-formulario .formulario p {
  font-size: 1.2rem;
  font-weight: bold;
  margin-bottom: 10px;
}
.contenedor-formulario .formulario input {
  height: 5.5rem;
  padding: 0 1rem;
  margin-bottom: 2rem;
  border-radius: 1.5rem;
  border: 0.1rem solid rgb(123, 123, 123);
}
.contenedor-formulario .formulario textarea {
  color: hsl(4, 100%, 67%);
}

.contenedor-imagen {
  order: 2;
}

.imagen-mobile {
  width: 100%;
}
@media (min-width: 777px) {
  .imagen-mobile {
    display: none;
  }
}

.imagen-desktop {
  display: none;
}
@media (min-width: 777px) {
  .imagen-desktop {
    display: block;
  }
}

* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  color: hsl(234, 29%, 20%);
}

body {
  background-color: hsl(234, 29%, 20%);
  display: grid;
  min-height: 100vh;
  place-content: center;
}

h1, h2 {
  font-size: 4.5rem;
  font-weight: 800;
  text-align: start;
  margin: 1rem 0;
}

.principal {
  background-color: white;
  border-radius: 4rem;
}
@media (min-width: 777px) {
  .principal {
    display: grid;
    gap: 1fr;
    grid-template-columns: repeat(2, 1fr);
    grid-template-columns: 2fr 1fr;
    max-width: 90rem;
    padding: 1.5rem;
  }
}

.secundaria {
  display: none;
  background-color: white;
  width: 100vw;
  height: 100vh;
  padding: 5rem 2.5rem;
  position: relative;
}
@media (min-width: 480px) {
  .secundaria {
    border-radius: 4rem;
    max-width: 45rem;
    height: 50rem;
    padding: 5rem;
  }
  .secundaria h2 {
    font-size: 5rem;
    line-height: 6rem;
    padding-bottom: 3rem;
  }
}
.secundaria img {
  width: 6rem;
}
.secundaria h2 {
  font-size: 4rem;
  line-height: 5rem;
  padding: 2rem 0;
}
.secundaria p {
  font-size: 1.5rem;
  line-height: 2rem;
  padding-bottom: 4rem;
}
.secundaria input {
  box-sizing: border-box;
  height: 5.5rem;
  padding: 0 1rem;
  margin-bottom: 2rem;
  border-radius: 1.5rem;
  border: 0.1rem solid rgb(123, 123, 123);
}

footer {
  color: white;
}
footer a {
  color: rgb(12, 220, 161);
}

.boton {
  color: white;
  font-weight: bold;
  font-size: 1.5rem;
  background-color: hsl(234, 29%, 20%);
  border: none;
  transition: transform 0.5s ease;
  width: 100%;
}
.boton:hover {
  background-image: linear-gradient(to right, hsl(4, 100%, 67%) 30%, hsl(24, 77%, 54%));
  cursor: pointer;
  transform: scale(1.02);
}

.mostrar {
  display: block;
}

.hidden {
  display: none;
}

.span-email {
  font-weight: bold;
}

.error {
  color: hsl(4, 100%, 67%);
}

.error-input {
  border: 0.1rem solid hsl(4, 100%, 67%) !important;
  background-color: rgb(231, 231, 231);
}

.error-input:focus {
  outline: 0.1em solid hsl(4, 100%, 67%);
}

.error-input::placeholder {
  color: hsl(4, 100%, 67%);
}

/** Pag INTERNAS **/